@charset "UTF-8";
.site-content {
  width: 1060px;
  padding: 30px 0; }

.main {
  flex: 1 1 0;
  max-width: 700px;
  padding-right: 50px;
  line-height: 24px; }

figure {
  font-family: "PTSerif-Regular"; }

h1,
h2,
h3,
h4 {
  font-family: "PTSerif-Bold";
  color: #1d5273;
  font-size: 24px;
  font-weight: 700;
  padding: 10px 0; }

h1 {
  font-size: 32px;
  line-height: 44px;
  border-bottom: 4px solid  #57a7dc;
  padding: 20px 0 10px;
  margin: 0; }

h3 {
  font-size: 20px; }

h3 {
  font-size: 18px; }

hr.blue {
  color: #57a7dc;
  margin: 0 0 40px;
  border: 1px solid #57a7dc; }

span.underline {
  color: #55a8de;
  text-decoration: underline; }

span.bold {
  font-family: "PTSans-Bold"; }

span.color {
  color: #2bd081; }

.post,
.advert,
.row-heading.top,
p {
  padding: 10px 0; }

.meta-span.dark,
a {
  color: #1d5273; }

.vnimanie {
  border-radius: 5px;
  background-color: #55a8de;
  border: 10px solid #eef8fd;
  font-family: "PTSans-Regular";
  color: #ffffff;
  font-size: 16px;
  padding: 30px 34px 30px 0;
  margin: 20px 0; }
  .vnimanie:before {
    content: "";
    display: inline-block;
    width: 59px;
    height: 70px;
    float: left;
    background-image: url(/images/control/bv_osnovnoi.png);
    margin: 0 40px 0px 40px; }

.main ul li {
  line-height: 22px;
  padding: 8px 0 8px 0; }
  .main ul li:before {
    content: "ïƒš";
    display: initial;
    font-family: "FontAwesome";
    color: #2bd081;
    font-size: 16px;
    padding: 0 19px; }

.main ol {
  counter-reset: list1; }
  .main ol li {
    padding: 8px 0 8px 0; }
  .main ol li li {
    padding: 10px 0 0 0; }
  .main ol li:before {
    counter-increment: list1;
    content: counters(list1, ".") ".";
    margin-right: 10px;
    color: #2bd081;
    font-family: "PTSerif-Regular"; }

.float-right {
  float: right;
  font-style: italic;
  font-size: 14px; }

.title-heading {
  width: 129px;
  background-image: url(/images/background/yarlik.png);
  background-repeat: no-repeat;
  padding: 8px 12px;
  margin: 0 0 10px 0;
  font-family: "PTSerif-Bold";
  color: #ffffff;
  font-size: 14px; }

.row-heading.top {
  height: 178px;
  margin-bottom: 40px; }
  .row-heading.top .heading-item {
    width: 95px;
    height: 95px;
    font-size: 13px; }
    .row-heading.top .heading-item:after {
      margin-top: 5px; }

.row-heading.bottom {
  flex-basis: 100%; }
  .row-heading.bottom .heading-item {
    width: 140px;
    height: 140px; }

.related {
  justify-content: space-between;
  padding: 20px 0; }
  .related .items {
    flex-basis: calc(100%/3 - 20px);
    background-color: rgba(241, 249, 253, 0.9);
    border: transparent;
    position: relative;
    transition: height 5.5s, background-color 2s; }
    .related .items img {
      width: 100%; }
    .related .items:hover {
      background-color: transparent;
      border: 1px solid #f1f9fd; }
      .related .items:hover img {
        height: 0px; }
      .related .items:hover .items-content {
        height: auto;
        overflow: visible;
        text-align: left; }
      .related .items:hover .items-title-articl {
        color: #496e86;
        text-shadow: 0.25px 0.1px 0px #496e86, -0.25px -0.1px 0px #496e86; }
      .related .items:hover .items-title-articl:after {
        position: initial;
        margin: 13px 0 0 0; }
      .related .items:hover .meta-span {
        color: #1d5273; }
  .related .items-content {
    height: 60px;
    overflow: hidden; }
  .related .items-title-articl:after {
    content: "";
    display: block;
    width: 20px;
    border: 1px solid #7bbfec;
    position: absolute;
    top: 68%;
    left: 0;
    right: 0; }
  .related .content {
    font-size: 14px; }

.publication {
  border: 1px solid #f1f9fd;
  padding: 20px 0;
  margin: 20px 0; }
  .publication.backhover:hover {
    background-color: #eff8fd; }
  .publication .picture {
    height: 200px;
    width: 230px;
    margin: 0 20px; }
  .publication .link-pub {
    font-family: "PTSans-Bold";
    font-size: 24px; }
    .publication .link-pub:hover {
      color: #77c1ee; }
    .publication .link-pub:after {
      content: "";
      display: block;
      width: 20px;
      border: 1px solid #7bbfec;
      margin: 10px 0; }
  .publication .piblicat {
    font-size: 14px;
    line-height: 18px; }
  .publication .meta {
    justify-content: flex-start; }
    .publication .meta .messag {
      margin-left: 20px; }
